










import { Vue, Component, Prop, Mixins, Watch } from "vue-property-decorator";
import TheLoginBox from "@/components/misc/TheLoginBox.vue";
import { notificationModule, authModule } from "@/store/modules";

@Component({
  components: {
    TheLoginBox
  }
})
export default class Login extends Mixins() {
  async mounted() {
    await authModule.checkIfAnyUserExists();
    authModule.setLoaded(true);
  }
  async submit(data: { password: string; username: string }) {
    try {
      const response = await authModule.authenticate(data);
      const redirect = this.$route.query.redirect;
      this.$router.push(
        redirect ? (Array.isArray(redirect) ? (redirect[0] || "/") : redirect) : "/"
      );
    } catch (err) {
      console.log(err);
    }
  }
  get anyUserExists() {
    return authModule.anyUserExists;
  }

  @Watch("anyUserExists", { immediate: true })
  handleUserExistsChange() {
    if (!this.anyUserExists) {
      this.$router.replace("/setup");
      authModule.setLoaded(true);
    }
  }
}
