










import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import HomeWizard from "@/components/home/HomeWizard.vue";
import TheLoginBox from "@/components/misc/TheLoginBox.vue";
import { authModule } from "@/store/modules";

@Component({
  components: {
    TheLoginBox,
    HomeWizard
  }
})
export default class SetupWizard extends Mixins() {
  async submit(data: {
    password: string;
    username: string;
    firstName: string;
    lastName: string;
    email: string;
  }) {
    const response = await authModule.createUser(data);
    this.$router.push(
      this.$route.query.redirect ? String(this.$route.query.redirect) : "/"
    );
  }

  async mounted() {
    authModule.setLoaded(true);
  }
}
